/**
 * Affichage des champs simples des tasks en lecture seule
 */
export interface TaskField extends BaseElement {
	initialize(init: OTaskFieldInit): this;
}

export interface OTaskFieldInit extends OSkinableInit {
	reg?: IReg<ITaskUiEnv>
	field: 'actTi' | 'tkDeadline' | 'scheduledDt' | 'lcSt'
}

export class TaskField extends BaseElement implements IView {
	reg: IReg<ITaskUiEnv>;

	config: OTaskFieldInit;

	_field: HTMLElement;

	_initialize(init: OTaskFieldInit) {
		this.reg = this.findReg(init);
		this.config = init;
		this.setAttribute("field", this.config.field);
		if (this.config.field === "actTi") {
			//na
		} else if (this.config.field === "lcSt") {
			this._field = this.appendChild(<c-label/>); //î={{noShadow:true} as OLabelableConfigs}
		} else {
			//tkDeadline et scheduledDt
			this._field = this.appendChild(<input/>);
		}
	}

	protected _refresh() {
		const itemType = this.reg.env.itemType;
		const longDesc = this.reg.env.longDesc;
		//console.log("longDesc:::::", longDesc);
		switch (this.config.field) {
		case 'actTi':
			DOM.setTextContent(this, longDesc.itTi || "");
			break;
		case 'lcSt':
			const lc = itemType.getLcStateOrUnknown(longDesc.lcSt);
			(this._field as Label).label = lc.name;
			(this._field as Label).icon = lc.iconUrl;
			break;
		case 'tkDeadline':
			DOM.setAttr(this._field, "value", longDesc.tkDeadline.toString());
			break;
		case 'scheduledDt':
			const ct = this.reg.env.taskContent;
			DOM.setAttr(this._field, "value", TASK.getScheduledDtFromContent(ct));
			break;
		}
	}

}

//REG.reg.registerSkin("wsp-task-field", 1, /* language=CSS */ ``);

customElements.define("wsp-task-field", TaskField);


/**
 * Affichage des champs simples des tasks en mode éditable.
 */
export interface TaskFieldInput extends BaseElement {
	initialize(init: OTaskFieldInit): this;
}

export interface OTaskFieldInputInit extends OSkinableInit {
	reg?: IReg<ITaskUiEnv>
	field: 'actTi' | 'tkDeadline' | 'scheduledDt'
	/** true par défaut */
	editable?: boolean
	/** Si true, accepte que le champ soit en erreur à l'enregistrement.
	 * false par défaut */
	permissive?: boolean
	writePerms?: string | string[]
}

export class TaskFieldInput extends BaseElement implements ITaskEditableField, IInfoConsumer {
	reg: IReg<ITaskUiEnv>;

	get fieldName() {return this.config.field}

	get value(): string {
		if (this.config.field === 'actTi') return this._input.value;
		const dt = this._input.valueAsDate;
		return dt ? dt.getTime().toString() : "";
	}

	get valueTime(): number {
		if (this.config.field === 'actTi') return 0;
		const dt = this._input.valueAsDate;
		return dt ? dt.getTime() : 0;
	}

	originalValue: string;

	get isDirty(): boolean {return this.originalValue !== this.value}

	config: OTaskFieldInputInit;

	_input: HTMLInputElement;

	currentStage?: ETaskStage;

	_initialize(init: OTaskFieldInputInit) {
		this.reg = this.findReg(init);
		const sr = this._attach(this.localName, init);
		this.config = init;
		this.setAttribute("field", this.config.field);
		const st = this.reg.env.initStates;
		if (this.config.field === "actTi") {
			this.originalValue = this.reg.env.longDesc.actTi || "";
			this._input = sr.appendChild(<input required/> as HTMLInputElement);
			if (st && st.ti != null) this._input.value = st.ti;
			else this._input.value = this.originalValue;
		} else {
			//tkDeadline et scheduledDt
			this._input = sr.appendChild(<input type="date"/> as HTMLInputElement);
			if (this.config.field === "scheduledDt") {
				this.originalValue = TASK.getScheduledDtFromContent(this.reg.env.taskContent);
				this._input.required = true;
				this.currentStage = this.reg.env.itemType.getLcStateOrUnknown(this.reg.env.longDesc.lcSt).taskStage;
				this._input.value = this.timeToInputValue(st && st.scheduledDt ? st && st.scheduledDt.toString() : this.originalValue);
			} else {
				const longDesc = this.reg.env.longDesc;
				this.originalValue = longDesc.tkDeadline > 0 ? longDesc.tkDeadline.toString() : "";
				this._input.value = this.timeToInputValue(st && st.deadlinedDt ? st && st.deadlinedDt.toString() : this.originalValue);
			}
		}
		this.addEventListener("input", this.onInputChange);
		this.reg.env.taskEditBroker.addConsumer(this);
	}

	onInfo(info: IInfo): void {
		if (info instanceof InfoReqStateTask) {
			if (this.config.field === 'scheduledDt') {
				if (this.currentStage === ETaskStage.forthcoming) {
					if (!info.isDirty) info.isDirty = this.isDirty;
					if (!info.isInError && this._input.valueAsDate == null && !this.config.permissive) info.isInError = true;
					if (info.states && this.isDirty) info.states.scheduledDt = this.valueTime;
				}
			} else {
				if (!info.isDirty) info.isDirty = this.isDirty;
				if (this.config.field === "actTi") {
					if (!info.isInError && !this.config.permissive) info.isInError = !this.value;
					if (info.states && this.isDirty) info.states.ti = this.value;
				} else {
					if (info.states && this.isDirty) info.states.deadlinedDt = this.valueTime;
				}
			}
		} else if (info instanceof InfoReqFillUpdates) {
			if (this.isDirty) {
				switch (this.config.field) {
				case 'actTi':
					info.updates.push(new SetTitle(this.value));
					break;
				case 'tkDeadline':
					info.updates.push(new SetDeadline(this.value));
					break;
				case 'scheduledDt':
					if (this.currentStage === ETaskStage.forthcoming) info.updates.push(new SetScheduleDt(this.value));
					break;
				}
			}
		} else if (info instanceof InfoChangeTask && info.field.fieldName === "lcSt" && this.config.field === "scheduledDt") {
			this.currentStage = this.reg.env.itemType.getLcStateOrUnknown(info.field.value).taskStage;
			this.refresh();
		}
	}

	protected onInputChange(ev: Event) {
		this.reg.env.taskEditBroker.dispatchInfo(new InfoChangeTask(this), this);
	}

	protected _refresh() {
		if (this.config.field === "scheduledDt") {
			const hidden = this.currentStage !== ETaskStage.forthcoming;
			TASK.hideContextField(this, hidden);
			if (hidden) return;
		}
		DOM.setAttrBool(this._input, "readonly", this.config.editable === false || (this.config.writePerms && !this.reg.hasPerm(this.config.writePerms)));
	}

	protected timeToInputValue(val: string): string {
		if (!val) return "";
		try {
			const dt = new Date(parseInt(val));

			function toStr(n: number) {return n < 10 ? "0" + n.toString() : n.toString()}

			return `${dt.getFullYear()}-${toStr(dt.getMonth() + 1)}-${toStr(dt.getDate())}`;
		} catch (e) {
			return "";
		}
	}

}

