/**
 * Champs XML des taches (affiché via un Wed et une house volatile)
 */
export interface TaskXmlField extends BaseElement {
	initialize(init: OTaskXmlFieldInit): this;
}

export interface OTaskXmlFieldInit extends OSkinableInit {
	reg?: IReg<ITaskUiEnv>
	/** true par défaut */
	editable?: boolean
	/** Si true, accepte que le champ soit en erreur à l'enregistrement.
	 * false par défaut */
	permissive?: boolean
	field: 'tkDescription' | 'tkComment'
	editorKey: string
	editorConfig?: OWedEditorBoxConfig
	wedMgrConfig?: OWedManagerConfig
	writePerms?: string | string[]
}

export class TaskXmlField extends BaseElementAsync implements IView, IViewContainer, ITaskEditableField, IInfoConsumer {
	reg: IReg<ITaskUiEnv>;

	view: WedEditorBox;

	house: IWspXmlHouse;

	fieldName: 'tkDescription' | 'tkComment';

	config: OTaskXmlFieldInit;

	get value(): Element {return this.house.document.documentElement}

	get isDirty(): boolean {return this._initedAsDirty || this.house.isDirty}

	protected _initedAsDirty: boolean;

	async _initialize(init: OTaskXmlFieldInit) {
		try {
			this.config = init;
			this.reg = this.findReg(init);
			this.fieldName = init.field;
			if (init.editable !== false) {
				this.reg.env.taskEditBroker.addConsumer(this);
				this.setAttribute("editable", "");
			}
			this.attachShadow(DOMSH.SHADOWDOM_INIT);
			this._initAndInstallSkin(this.localName, init);

			const wsp = this.reg.env.wsp;
			const longDesc = this.reg.env.longDesc;
			const itemType = wsp.wspMetaUi.getItemType(longDesc.itModel) as ItemTypeTask;

			const dataEditor = itemType.getEditor(init.editorKey);
			if (!dataEditor) throw Error("No editor found for task desc: " + init.editorKey);

			const editorConfig = Object.assign({}, init.editorConfig) as OWedEditorBoxConfig & OWedEditorDiffBarConfig;
			const wedMgrConfig = Object.assign({}, init.wedMgrConfig) as OWedManagerConfig;

			//if (!editorConfig.metaBarMode) editorConfig.metaBarMode = 'available';

			if (!wedMgrConfig.fetchContentThreshold) wedMgrConfig.fetchContentThreshold = 6000;
			if (!wedMgrConfig.dataTransferAnalyzer) wedMgrConfig.dataTransferAnalyzer = WspDataTransferWedAnalyzer.SINGLETON;

			this.view = new WedEditorBox();

			if (dataEditor.initReg) {
				//Customisation du reg pour l'editor.
				this.reg = REG.createSubReg(this.reg);
				await dataEditor.initReg(this.reg, this.view);
			}

			if (dataEditor.overrideConfigs) await dataEditor.overrideConfigs(editorConfig, wedMgrConfig, this.view);

			//Après dataEditor.overrideConfigs() pour empêcher de surcharger editorConfig.reg qui doit être fait dans dataEditor.initReg()
			if (!editorConfig.reg) editorConfig.reg = this.reg;

			//chargement asynchrone en // des librairies standard wed et des wedModels
			this.view.addAsyncLib(boxTagsDefined);
			this.view.addAsyncLib(txtTagsDefined);
			this.view.addAsyncLib(ptrItemTagsDefined);
			const mainEndP = itemType.getWed(dataEditor.mainWed);
			this.view.addAsyncLib(WED.loadWedModel(mainEndP, mainEndP).then((wed) => this.view.wedModel = wed));
			// if (dataEditor.outlineWed) {
			// 	let outlineEndP = itemType.getWed(dataEditor.outlineWed);
			// 	this.view.addAsyncLib(WED.loadWedModel(outlineEndP, outlineEndP).then((wed) => this.view.outlineWedModel = wed));
			// }
			// if (dataEditor.previewWed) {
			// 	let altviewEndP = itemType.getWed(dataEditor.previewWed);
			// 	this.view.addAsyncLib(WED.loadWedModel(altviewEndP, altviewEndP).then((wed) => this.view.previewWedModel = wed));
			// }

			wedMgrConfig.readOnlyCauses = [];
			editorConfig.hideDiffBar = true; //Pas de diff sur une tâche
			editorConfig.withoutScrolls = true; //XXX Permettre de pointer un scroll parent
			if (init.editable === false) {
				wedMgrConfig.readOnlyCauses.push("initView");
				editorConfig.hideCommonBar = true;
				editorConfig.hideErrBar = true;
				editorConfig.hideUnknownBar = true;
				editorConfig.disableBoxSelection = true;
				editorConfig.disableVirtuals = true;
				(editorConfig as OWedEditorTxtConfig).hideStackBar = true;
			} else if (!this.reg.hasPerm(init.writePerms || "write.task.update")) {
				wedMgrConfig.readOnlyCauses.push("perms");
			}

			let doc: Document;
			const st = this.reg.env.initStates;
			let schema: Schema;
			if (init.field === "tkDescription") {
				schema = await itemType.getDesriptionSchema();
				if (st && st.desc) {
					this._initedAsDirty = true;
					doc = DOM.parseDomValid(st.desc);
				}
				if (!doc) {
					const originalDesc = TASK.getDescriptionFromContent(this.reg.env.taskContent);
					//console.log("originalDesc::::::", originalDesc);
					doc = DOM.newDomDoc();
					if (originalDesc) doc.appendChild(doc.importNode(originalDesc, true));
					else doc.appendChild(doc.createElementNS(TASK.TASK_NS, TASK.TAG_setDescription));
				}
			} else {
				schema = await itemType.getCommentSchema();
				if (st && st.cmt) {
					this._initedAsDirty = true;
					doc = DOM.parseDomValid(st.cmt);
				}
				if (!doc) {
					doc = DOM.newDomDoc();
					doc.appendChild(doc.createElementNS(TASK.TASK_NS, TASK.TAG_addComment));
				}
			}
			this.house = await wsp.wspServer.wspsLive.newLocalHouse({
				initialDoc: doc,
				schema,
				buildOptions: {genAnnots: init.editable !== false, autoMutate: true, autoComplete: true, autoNormXml: true, autoNormChars: true, autoCleanup: true}
			}, wsp);
			this.view.initialize(editorConfig);
			if (await this.view.initFromDocHolder(new DocHolder(this.house), wedMgrConfig)) {
				this.shadowRoot.appendChild(this.view);
			} else {
				throw Error("Init editor failed");
			}

			this.house.listeners.on("dirtyChange", () => {this.reg.env.taskEditBroker.dispatchInfo(new InfoChangeTask(this), this)})
		} catch (e) {
			console.log(e);
			this.closeEditor();
		}
	}

	connectedCallback() {
		super.connectedCallback();
		// Comment masqué à la création
		if (this.fieldName === 'tkComment' && SRC.isNewSrcUri(this.reg.env.longDesc.srcUri)) {
			TASK.hideContextField(this, true);
		}
	}

	onInfo(info: IInfo): void {
		if (info instanceof InfoReqStateTask) {
			switch (this.fieldName) {
			case 'tkDescription':
				if (!info.isInError && !this.config.permissive) info.isInError = this.house.schemaDom.getAllAnnots().find((a) => a.level.weight >= EAnnotLevel.error.weight) != null;
				if (info.states) {
					if (this.isDirty) {
						info.isDirty = true;
						info.states.desc = DOM.ser(this.house.getDocumentForSave());
					}
				} else if (!info.isDirty) info.isDirty = this.isDirty;
				break;
			case 'tkComment':
				if (info.states) {
					if (!this.isCommentEmpty()) {
						info.isDirty = true;
						info.states.cmt = DOM.ser(this.house.getDocumentForSave());
					}
				} else if (!info.isDirty) info.isDirty = !this.isCommentEmpty();
				break;
			}
		} else if (info instanceof InfoReqFillUpdates) {
			if (this.isDirty) {
				switch (this.fieldName) {
				case 'tkDescription':
					info.updates.push(new SetDescription(this.value));
					break;
				case 'tkComment':
					if (!this.isCommentEmpty()) {
						info.updates.push(new AddComment(this.value));
					}
					break;
				}
			}
		}
	}

	isCommentEmpty(): boolean {
		let isEmpty = true;
		this.view.wedMgr.rootWedlet.visitWedletChildren(0, Infinity, (w: IChildWedlet) => {
			if (w.model.nodeName === TASK.TAG_addComment) {
				(w as IParentWedlet).visitWedletChildren(0, Infinity, (w: IChildWedlet) => {
					if (w.model.nodeType === ENodeType.element) {
						(w as IParentWedlet).visitWedletChildren(0, Infinity, (w: IChildWedlet) => {
							if (!w.isEmpty()) {
								isEmpty = false;
								return 'stop';
							}
						});
						return 'stop';
					}
				});
				return 'stop';
			}
		});
		return isEmpty;
	}

	closeEditor() {
		if (this.house) {
			this.reg.env.universe.wspServer.wspsLive.removeLocalHouse(this.house);
			this.house = null;
		}
		if (!this.view) {
			this.view.wedMgr.killEditor();
			if (this.view.parentNode) this.view.remove();
			this.view = null;
		}
	}

	onViewHidden(closed?: boolean) {
		if (closed) this.closeEditor();
	}

	visitViews(visitor: (view: IView) => any): any {
		if (this.view) return visitor(this.view);
	}

	visitViewsAsync(visitor: (view: IView) => Promise<any>, options?: OViewVisitOptions): Promise<any> {
		return this.view ? visitor(this.view) : Promise.resolve(undefined);
	}
}

