customElements.define("wsp-task-xmlfield", TaskXmlField);

/**
 * Bouton d'enregistrement d'une édition d'un formulaire (à placer généralement avec wsp-task-cancel).
 */
export class TaskSaveBtn extends Button implements IInfoConsumer {
	reg: IReg<ITaskUiEnv>;

	_initialize(init: OButtonInit) {
		this.reg = this.findReg(init);
		setDefaultBtn(init, "Valider les modifications", "/@skin@/wsp/views/tasks/save.svg");
		this.hidden = true;
		super._initialize(init);
		this.onclick = this.onClick;
		this.reg.env.taskEditBroker.addConsumer(this);
	}

	onInfo(info: IInfo): void {
		if (info instanceof InfoChangeTask) {
			this.disabled = info.state.isInError;
			if (DOM.setHidden(this, !info.state.isDirty)) {
				this.reg.env.srcView.setNeedValid(info.state.isDirty);
				this.refresh();
			}
		}
	}

	async onClick(ev: MouseEvent) {
		if (this.hidden) return;
		if (this.disabled) {
			POPUP.showNotifWarning("Des erreurs doivent être corrigées avant de pouvoir valider les modifications.", this);
			return;
		}
		const env = this.reg.env;
		const req = new InfoReqFillUpdates();
		env.taskEditBroker.dispatchInfo(req, this);
		if (req.updates.length > 0) {
			try {
				const {srcUri} = await new MsgOver().setStandardMsg("saving").showMsgOver(env.srcView).waitFor<JSrcFields>(
					TASK.saveTask(env.wsp, this, SRC.srcRef(env.longDesc), req.updates)
				);
				if (srcUri !== env.longDesc.srcUri) {
					//cas d'une création de task.
					//console.log("new srcUri:::",srcUri);
					this.reg.env.srcView.resetMainView();
					this.reg.env.infoBroker.dispatchInfo(new InfoSrcUriMoved(env.longDesc.srcUri, srcUri), this);
				}
			} catch (e) {
				ERROR.report("Échec à la validation de la tâche", e);
				throw e;
			}
		}
	}
}

customElements.define("wsp-task-save", TaskSaveBtn);

/**
 * Bouton d'annulation d'une édition d'un formulaire (pendant de wsp-task-save).
 */
export class TaskCancelBtn extends Button implements IInfoConsumer {
	reg: IReg<ITaskUiEnv>;

	_initialize(init: OButtonInit) {
		this.reg = this.findReg(init);
		setDefaultBtn(init, "Annuler les modifications", "/@skin@/wsp/views/tasks/cancel.svg");
		this.hidden = true;
		super._initialize(init);
		this.onclick = this.onClick;
		this.reg.env.taskEditBroker.addConsumer(this);
	}

	onInfo(info: IInfo): void {
		if (info instanceof InfoChangeTask) {
			this.hidden = !info.state.isDirty || SRC.isNewSrcUri(this.reg.env.longDesc.srcUri);
		}
	}

	async onClick(ev: MouseEvent) {
		if (this.hidden) return;
		if (await POPUP.confirm("Confirmez l'abandon des modifications de cette tâche", this, {okLbl: "Abandonner mes changements"})) {
			this.reg.env.srcView.redrawMainView();
		}
	}

}

customElements.define("wsp-task-cancel", TaskCancelBtn);


/**
 * Bouton de suppression définitive d'une task.
 */
export class TaskDeleteBtn extends Button implements IInfoConsumer {
	reg: IReg<ITaskUiEnv>;

	_initialize(init: OButtonInit) {
		this.reg = this.findReg(init);
		setDefaultBtn(init, "Supprimer cette tâche", "/@skin@/wsp/views/tasks/delete.svg");
		super._initialize(init);
		const hasPerm = this.reg.hasPerm("ui.task.fields.deleteBtn");
		if (hasPerm) {
			this.onclick = this.onClick;
			this.reg.env.taskEditBroker.addConsumer(this);
		}
		DOM.setAttrBool(this, "hidden", !hasPerm);
	}

	onInfo(info: IInfo): void {
		if (info instanceof InfoChangeTask) {
			this.hidden = info.state.isDirty;
		}
	}

	async onClick(ev: MouseEvent) {
		if (this.hidden) return;
		if (await POPUP.confirm("Souhaitez-vous vraiment supprimer DÉFINITIVEMENT cette tâche ?", this, {okLbl: "Supprimer"})) {
			const env = this.reg.env;
			new MsgOver().setStandardMsg("deleting").showMsgOver(env.srcView).waitFor(
				TASK.deleteTask(env.wsp, this, [env.longDesc.srcId])
			);
		}
	}
}

customElements.define("wsp-task-delete", TaskDeleteBtn);

function setDefaultBtn(init: OButtonInit, label: string, icon: string) {
	if (init.uiContext === 'dialog') {
		if (init.label === undefined) init.label = label;
	} else {
		if (init.title === undefined) init.title = label;
	}
	if (init.icon === undefined) init.icon = icon;
}

/**
 * Bouton d'exécution d'une transition précise, indépendamment de tout formulaire
 * et autres input en cours d'édition et bouton wsp-task-save.
 */
export interface TaskExecTransBtn extends Button {
	initialize(init: OTaskExecTransBtnInit): this;
}

export interface OTaskExecTransBtnInit extends OButtonInit {
	reg?: IReg<ITaskUiEnv>
	transition: string
	noConfirm?: boolean
}

export class TaskExecTransBtn extends Button {
	reg: IReg<ITaskUiEnv>;

	config: OTaskExecTransBtnInit;

	trans: SrcExecTransition<IShortDescCtx>;

	_initialize(init: OTaskExecTransBtnInit) {
		this.config = init;
		this.reg = this.findReg(init);
		super._initialize(init);
		const trans = this.reg.env.itemType.getLcTransition(this.config.transition);
		if (trans) {
			this.trans = new SrcExecTransition(trans, this.reg.env.itemType);
			this.onclick = this.onClick;
			if (!this.label) this.label = trans.name;
		}
	}

	buildInitFromAtts(init?: OTaskExecTransBtnInit): any {
		init = super.buildInitFromAtts(init) as OTaskExecTransBtnInit;
		init.transition = this.getAttribute("transition");
		init.noConfirm = this.getAttribute("confirm") === "no";
		return init;
	}

	async onClick(ev: MouseEvent) {
		if (this.config.noConfirm || await POPUP.confirm(`Confirmez votre choix '${this.label}'`, this)) {
			try {
				await TASK.saveTask(this.reg.env.wsp, this, this.reg.env.longDesc.srcId, [new ExecTrans(this.config.transition)]);
			} catch (e) {
				ERROR.report("Échec à l'exécution de la transition", e);
				throw e;
			}
		}
	}

	protected _refresh() {
		if (this.trans && this.trans.isVisible(this.reg.env.srcView)) {
			DOM.setHidden(this, false);
			DOM.setAttrBool(this, 'disabled', !this.trans.isEnabled(this.reg.env.srcView));
			super._refresh();
		} else {
			DOM.setHidden(this, true);
		}
	}

}

