customElements.define("wsp-task-exec-trans", TaskExecTransBtn);


/** Requete auprès des champs pour connaitre du formulaire d'édition. */
export class InfoReqStateTask implements IInfo {

	isDirty?: boolean;
	isInError?: boolean;

	/** Si non null les fileds doivent renseigner les valeurs si ils sont dirty. */
	states: JTaskStates;

	infoHolders?: IInfoProducer[];
}

/** Notification d'une édition d'un champ. */
export class InfoChangeTask implements IInfo {

	protected _state: InfoReqStateTask;
	get state(): InfoReqStateTask {
		if (!this._state) {
			this._state = new InfoReqStateTask();
			this.field.reg.env.taskEditBroker.dispatchInfo(this._state, null);
		}
		return this._state;
	}

	constructor(public field: ITaskEditableField) {}

	infoHolders?: IInfoProducer[];
}


/** Requete pour constituer la liste des modifications à appliquer. */
export class InfoReqFillUpdates implements IInfo {
	updates: IUpdateTask[] = [];

	infoHolders?: IInfoProducer[];
}

interface ITaskEditableField {
	reg: IReg<ITaskUiEnv>
	fieldName: 'actTi' | 'tkDeadline' | 'scheduledDt' | 'lcSt' | 'tkDescription' | 'tkComment' | 'resps' | 'init'
	value?: any
}
